"use client";

import { useState, useRef, useEffect } from "react";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { ChevronLeft, Send } from "lucide-react";

// Mock data for messages
const MOCK_MESSAGES = {
  "1": [
    { id: "1", text: "Hey, how are you?", sender: "them", timestamp: "10:30 AM" },
    { id: "2", text: "I'm good, thanks! How about you?", sender: "me", timestamp: "10:31 AM" },
    { id: "3", text: "Pretty good! Working on some new features.", sender: "them", timestamp: "10:32 AM" },
  ],
  "2": [
    { id: "1", text: "Meeting at 3 PM", sender: "them", timestamp: "9:45 AM" },
    { id: "2", text: "I'll be there!", sender: "me", timestamp: "9:46 AM" },
  ],
  "3": [
    { id: "1", text: "Great idea! Let's discuss tomorrow", sender: "them", timestamp: "Yesterday" },
    { id: "2", text: "Sounds good to me", sender: "me", timestamp: "Yesterday" },
  ],
};

const MOCK_CHAT_INFO = {
  "1": { name: "John Doe", avatar: "https://images.pexels.com/photos/771742/pexels-photo-771742.jpeg" },
  "2": { name: "Jane Smith", avatar: "https://images.pexels.com/photos/415829/pexels-photo-415829.jpeg" },
  "3": { name: "Team Discussion", avatar: "https://images.pexels.com/photos/1043471/pexels-photo-1043471.jpeg" },
};

interface ChatWindowProps {
  chatId: string;
  onBack: () => void;
}

export default function ChatWindow({ chatId, onBack }: ChatWindowProps) {
  const [newMessage, setNewMessage] = useState("");
  const [messages, setMessages] = useState(MOCK_MESSAGES[chatId as keyof typeof MOCK_MESSAGES] || []);
  const chatInfo = MOCK_CHAT_INFO[chatId as keyof typeof MOCK_CHAT_INFO];
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Scroll to bottom when messages change
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTop = scrollAreaRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSendMessage = () => {
    if (!newMessage.trim()) return;
    
    // Add new message to the list
    const newMsg = {
      id: String(Date.now()),
      text: newMessage,
      sender: "me",
      timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
    };
    
    setMessages([...messages, newMsg]);
    setNewMessage("");
  };

  return (
    <div className="flex flex-col h-full">
      {/* Chat Header */}
      <div className="p-4 border-b flex items-center space-x-4">
        <Button
          variant="ghost"
          size="icon"
          className="md:hidden"
          onClick={onBack}
        >
          <ChevronLeft className="h-6 w-6" />
        </Button>
        <Avatar>
          <AvatarImage src={chatInfo?.avatar} alt={chatInfo?.name} />
          <AvatarFallback>{chatInfo?.name[0]}</AvatarFallback>
        </Avatar>
        <div>
          <h2 className="font-medium">{chatInfo?.name}</h2>
          <p className="text-sm text-gray-500">Online</p>
        </div>
      </div>

      {/* Messages */}
      <ScrollArea className="flex-1">
        <div className="p-4 space-y-4" ref={scrollAreaRef}>
          {messages.map((message) => (
            <div
              key={message.id}
              className={`flex ${
                message.sender === "me" ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`max-w-[70%] rounded-lg p-3 ${
                  message.sender === "me"
                    ? "bg-blue-500 text-white"
                    : "bg-gray-100"
                }`}
              >
                <p>{message.text}</p>
                <p
                  className={`text-xs mt-1 ${
                    message.sender === "me"
                      ? "text-blue-100"
                      : "text-gray-500"
                  }`}
                >
                  {message.timestamp}
                </p>
              </div>
            </div>
          ))}
        </div>
      </ScrollArea>

      {/* Message Input */}
      <div className="p-4 border-t flex items-center space-x-2">
        <Input
          type="text"
          placeholder="Type a message..."
          value={newMessage}
          onChange={(e) => setNewMessage(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === "Enter" && !e.shiftKey) {
              e.preventDefault();
              handleSendMessage();
            }
          }}
          className="flex-1"
        />
        <Button onClick={handleSendMessage} size="icon">
          <Send className="h-4 w-4" />
        </Button>
      </div>
    </div>
  );
}
