"use client";

import { useState } from "react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Badge } from "@/components/ui/badge";

// Mock data for chats
const MOCK_CHATS = [
  {
    id: "1",
    name: "John Doe",
    lastMessage: "Hey, how are you?",
    timestamp: "10:30 AM",
    avatar: "https://images.pexels.com/photos/771742/pexels-photo-771742.jpeg",
    unreadCount: 2,
  },
  {
    id: "2",
    name: "Jane Smith",
    lastMessage: "Meeting at 3 PM",
    timestamp: "9:45 AM",
    avatar: "https://images.pexels.com/photos/415829/pexels-photo-415829.jpeg",
    unreadCount: 0,
  },
  {
    id: "3",
    name: "Team Discussion",
    lastMessage: "Great idea! Let's discuss tomorrow",
    timestamp: "Yesterday",
    avatar: "https://images.pexels.com/photos/1043471/pexels-photo-1043471.jpeg",
    unreadCount: 5,
  },
];

interface ChatSidebarProps {
  onSelectChat: (chatId: string) => void;
}

export default function ChatSidebar({ onSelectChat }: ChatSidebarProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const filteredChats = MOCK_CHATS.filter((chat) =>
    chat.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="flex flex-col h-full">
      {/* Header */}
      <div className="p-4 border-b">
        <Input
          type="text"
          placeholder="Search chats..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="w-full"
        />
      </div>

      {/* Chat List */}
      <ScrollArea className="flex-1">
        <div className="space-y-0">
          {filteredChats.map((chat) => (
            <div
              key={chat.id}
              className="w-full p-4 flex items-center space-x-4 hover:bg-gray-100 transition-colors border-b border-gray-100 cursor-pointer"
              onClick={() => onSelectChat(chat.id)}
              role="button"
              tabIndex={0}
              onKeyDown={(e) => {
                if (e.key === 'Enter' || e.key === ' ') {
                  onSelectChat(chat.id);
                }
              }}
            >
              <Avatar>
                <AvatarImage src={chat.avatar} alt={chat.name} />
                <AvatarFallback>{chat.name[0]}</AvatarFallback>
              </Avatar>
              <div className="flex-1 text-left">
                <div className="flex justify-between items-center">
                  <h3 className="font-medium">{chat.name}</h3>
                  <span className="text-xs text-gray-500">{chat.timestamp}</span>
                </div>
                <p className="text-sm text-gray-500 truncate">{chat.lastMessage}</p>
              </div>
              {chat.unreadCount > 0 && (
                <Badge variant="secondary" className="rounded-full">
                  {chat.unreadCount}
                </Badge>
              )}
            </div>
          ))}
        </div>
      </ScrollArea>
    </div>
  );
}
